import React, { Component } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import axios from "axios";
import {ToastContainer,toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//YAPILACAK -> Fetch() ile (yada ajax) verileri gönder ve server da console log la

class YeniProje extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ProjeBaslik: "",
      Aciklama: "",
      GorevliListesi: [],
      BaslangicTarih: new Date(),
      BitisTarih: new Date(),
      Userlar: null
    };
  }
  Error = (mesaj) => {toast.error(mesaj)}
  Success = (mesaj) => {toast.success(mesaj)}

  componentDidMount() {
    this.TumUserHandler();
  }

  TumUserHandler = () => {
    if (this.props.token !== null) {
      axios
        .get("http://localhost:3010/tumUserlar", {
          headers: {
            "Content-Type": "application/json",
            authorization: this.props.token
          }
        })
        .then(response => response.data)
        .then(response =>
          {
            if(response.success === false)
            {
                this.Error("Hata Yaşandı -> "+response.mesaj);
                return;
            }
            this.setState({
            Userlar: (
              <select style={{ alignSelf: "left" }}>
                {response.map((user, index) => {
                  return (
                    <option
                      key={index}
                      value={user}
                      onClick={() => this.GorevliEkleHandler(user)}
                    >
                      {user.Ad + " " + user.Soyad}
                    </option>
                  );
                })}
              </select>
            )
          })
        }
        );
    }
  };

  //Not: user 2 şekilde eklenebilir birisi DB ye Array içinde user Objects eklenmesi
  //     yada user ad ve soyad String çevirilip yan yana yazılıp kayıt eklenmesi
  // ilki tercih edilecek ilerde verinin formatlanması daha kolay olur
  GorevliEkleHandler = user => {
    this.setState({
      GorevliListesi: [...this.state.GorevliListesi, user]
    });
  };

  silHandler = index => {
    const persons = [...this.state.GorevliListesi];
    persons.splice(index, 1);
    this.setState({ GorevliListesi: persons });
  };

  ProjeGonderHandeler = () => {
    const data = { ...this.state };
    //data.BaslangicTarih =data.BaslangicTarih.toISOString().split('T')[0];
    //data.BitisTarih = data.BitisTarih.toISOString().split('T')[0];
    delete data["Userlar"];
    //Burdaki authorization daki TOKEN i postmandan getirdim
    //Yeni Proje Sayfasını arayüz ile birileştir ve app.js e gereken global state token değişkeni oluştur
    axios
      .post("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/yeniProjeKayitEt", data, {
        headers: {
          "Content-Type": "application/json",
          authorization: this.props.token
        }
      })
      .then(response => response.data)
      .then(response => {
        if(response.success === true)
        {
          this.Success(response.mesaj)
        }
        else
        {
          this.Error(response.mesaj)
        }
        
      })
      .catch(error => this.Error("Hata yaşandı -> "+error));
    //Burda fetch çalıştır ve veri gönder display et
  };

  render() {
    return (
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "column"
        }}
      >
        <ToastContainer/>
        <h1>Proje Ekleme Sayfasını</h1>
        {/*Projede Başlık ve Açıklamaları*/}
        <label>Proje Başlık</label>
        <input
          type="text"
          name="ProjeBaslik"
          value={this.state.ProjeBaslik}
          onChange={event => this.setState({ ProjeBaslik: event.target.value })}
          style={{ width: "500px", height: "20px" }}
        />
        <label>Açıklama</label>
        <textarea
          name="Aciklama"
          value={this.state.Aciklama}
          onChange={event => this.setState({ Aciklama: event.target.value })}
          style={{ width: "500px", height: "200px" }}
        />
        {/*Projede Görev alack olan kişler*/}
        <label>Gorevliler</label>

        <div>
          {this.state.Userlar !== null ? (
            this.state.Userlar
          ) : (
            <p>Görevli Listesini Görebilmek için Lütfen Giriş Yapınız!</p>
          )}
        </div>

        {/*Burda Girilen Kullanıcılar Listeleniyor*/}
        <div style={{ marginTop: "20px", marginBottom: "80px" }}>
          {this.state.GorevliListesi.length > 0 ? (
            this.state.GorevliListesi.map((veri, index) => (
              <li key={index}>
                {veri.Ad + " " + veri.Soyad}
                <button
                  onClick={this.silHandler.bind(this, index)}
                  style={{ marginLeft: "30px" }}
                >
                  SİL
                </button>
              </li>
            ))
          ) : (
            <li>Görevliler Listesi</li>
          )}
        </div>
        {/*Başlangıc ve Bitiş Tarihleri*/}
        <div>
          <label style={{ marginRight: "10px" }}>BaslangicTarih</label>
          <DatePicker
            selected={this.state.BaslangicTarih}
            dateFormat="yyyy/MM/dd"
            minDate={new Date()}
            onChange={date => this.setState({ BaslangicTarih: date })}
          />
          <label style={{ marginRight: "10px", marginLeft: "40px" }}>
            BitisTarih
          </label>
          <DatePicker
            selected={this.state.BitisTarih}
            dateFormat="yyyy/MM/dd"
            minDate={this.state.BaslangicTarih}
            onChange={date => this.setState({ BitisTarih: date })}
          />
        </div>
        <button
          style={{ marginTop: "100px", width: "90px", height: "40px" }}
          onClick={this.ProjeGonderHandeler}
        >
          Kayıt Et
        </button>
      </div>
    );
  }
}

export default YeniProje;
