import React, { Component } from "react";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

class KullaniciEkle extends Component {
  constructor(props) {
    super(props);
    this.state = {
      KullaniciAdi: "",
      Ad: "",
      Soyad: "",
      Sifre: "",
      Sifre_Tekrar: ""
    };
  }

  Error = mesaj => {
    toast.error(mesaj);
  };
  Success = mesaj => {
    toast.success(mesaj);
  };

  KullaniciEkleHandler = () => {
    if (this.state.KullaniciAdi === null || this.state.KullaniciAdi === "") {
      this.Error("Kullanıcı Adı alanı boş");
      return;
    }
    if (this.state.Ad === null || this.state.Ad === "") {
      this.Error("Ad alanı boş");
      return;
    }
    if (this.state.Soyad === null || this.state.Soyad === "") {
      this.Error("Soyad alanı boş");
      return;
    }
    if(this.state.Sifre === null || this.state.Sifre === "")
    {
      this.Error("Şifre alanları boş");
      return;
    }
    if(this.state.Sifre_Tekrar === null || this.state.Sifre_Tekrar === "")
    {
      this.Error("Şifre alanları boş");
      return;
    }
    if (this.state.Sifre !== this.state.Sifre_Tekrar) {
      this.Error("Şifreler Eşlemedi");
      return;
    }
    let data = { ...this.state };
    delete data["Sifre_Tekrar"];
    data["Rol"] = "Calisan";
    console.log(data);
    axios
      .post("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/kullaniciEKle", data, {
        headers: {
          "Content-Type": "application/json",
          authorization: this.props.token
        }
      })
      .then(response => response.data)
      .then(response => this.Success(response.mesaj))
      .catch(error => this.Error("Hata Yaşandı -> " + error));
  };

  render() {
    return (
      <div style={{ display: "flex", flexDirection: "column" }}>
        <ToastContainer />
        <h1>Kullanıcı Ekle Sayfası</h1>
        <div>
          <label style={{ marginRight: "15px" }}>Kullanıcı Adınız </label>
          <input
            type="text"
            name="KullaniciAdi"
            value={this.state.KullaniciAdi}
            placeholder="Kullanıcı Adınız"
            onChange={event =>
              this.setState({ KullaniciAdi: event.target.value })
            }
          />
        </div>
        <div>
          <label style={{ marginRight: "15px" }}>Adınız</label>
          <input
            type="text"
            name="Ad"
            value={this.state.Ad}
            placeholder="Adınız"
            onChange={event => this.setState({ Ad: event.target.value })}
          />
        </div>
        <div>
          <label style={{ marginRight: "15px" }}>Soyadınız </label>
          <input
            type="text"
            name="Soyadiniz"
            value={this.state.Soyad}
            placeholder="Soyadınız"
            onChange={event => this.setState({ Soyad: event.target.value })}
          />
        </div>
        <div>
          <label style={{ marginRight: "30px" }}>Şifre </label>
          <input
            type="password"
            name="Sifre"
            value={this.state.Sifre}
            onChange={event => this.setState({ Sifre: event.target.value })}
          />
        </div>
        <div>
          <label style={{ marginRight: "30px" }}>Şifre tekrar </label>
          <input
            type="password"
            name="Sifre"
            value={this.state.Sifre_Tekrar}
            onChange={event =>
              this.setState({ Sifre_Tekrar: event.target.value })
            }
          />
        </div>
        <div>
          <button onClick={this.KullaniciEkleHandler}>Kullanıcı Ekle</button>
        </div>
      </div>
    );
  }
}

export default KullaniciEkle;
