import React, { Component } from "react";
//import logo from "./logo.svg";
import "./App.css";
import Login from "./Screens/Login";
//import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import YeniProje from "./Screens/YeniProje";
import KullaniciEkle from "./Screens/KullaniciEkle";
import GunlukAktiviteRaporu from "./Screens/GunlukAktiviteRaporu"
import GunlukAktiviteRaporuListele from "./Screens/GunlukAktiviteRaporuListele"
import RaporAyarlar from "./Screens/RaporAyarlar";

//NOT:Burdaki tokenYokEtHandeler etkisi ve kesinliği belli değil hata olması durumunda sorumlusu bu 
//olabilir

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      degistir: "Login",
      token: null,
      Ad:"",
      Soyad:"",
      _id:""
    };
  }
  timer=null;
  degistirHandler = (durum) => {
    this.setState({ degistir: durum });
  };

  setAd = (Ad) => {
    this.setState({ Ad: Ad });
  };
  setSoyad = (Soyad) => {
    this.setState({ Soyad: Soyad });
  };
  set_id = (id) =>{
    this.setState({_id: id});
  }

  tokenTimerHandler = () => {
    this.timer=setTimeout(() => {
      localStorage.removeItem("token");
      alert("Token Süresi Doldu! Tekrar Giriş Yapınız!");
      this.degistirHandler("Login");
      this.setState({token:null});
    }, 3600*1000);
  };

  tokenYokEtHandler= () =>{
    clearTimeout(this.timer);
  }

  setTokenHandler = gelenToken => {
    localStorage.setItem("token", gelenToken);
    this.tokenTimerHandler();
    this.setState({ token: gelenToken });
  };

  render() {
    let ekran = null;
    if (this.state.degistir === "YeniProje") {
      ekran = <YeniProje token={this.state.token} />;
    } else if (this.state.degistir === "Login") {
      ekran = <Login setToken={this.setTokenHandler} setAd={this.setAd} setSoyad={this.setSoyad} set_id={this.set_id} tokenYokEtHandler={this.tokenYokEtHandler}/>;
    } else if(this.state.degistir === "KullaniciEkle"){
      ekran = <KullaniciEkle token={this.state.token}/>
    }
    else if(this.state.degistir === "GunlukAktiviteRaporu"){
      ekran = <GunlukAktiviteRaporu token={this.state.token} Ad={this.state.Ad} Soyad={this.state.Soyad} _id={this.state._id}/>
    }
    else if(this.state.degistir === "GunlukAktiviteRaporuListele"){
      ekran = <GunlukAktiviteRaporuListele token={this.state.token} KullaniciAdi={this.state.KullaniciAdi}/>
    }
    else if(this.state.degistir === "RaporAyarlar")
    {
      ekran = <RaporAyarlar token={this.state.token}/>
    }
    return (
      <div
        style={{
          width: "100%",
          height: "100%",
          display: "flex",
          flexDirection: "row"
        }}
      >
        <div style={{ width: "14%", height: "100%", marginRight:"10px" }}>
          <button
            onClick={() => this.degistirHandler("Login")}
            style={{ width: "100%" }}
          >
            Login
          </button>
          <button style={{ width: "100%" }} onClick={() => this.degistirHandler("KullaniciEkle")}>
            Yeni Kullanıcı Ekle
          </button>
          <button style={{ width: "100%" }} onClick={() => this.degistirHandler("RaporAyarlar")}>
            Rapor Ayarlar
          </button>
          <button style={{ width: "100%" }} onClick={() => this.degistirHandler("YeniProje")}>
            Yeni Proje Ekle
          </button>
          <button style={{ width: "100%" }} onClick={() => this.degistirHandler("GunlukAktiviteRaporu")}>
            Yeni Günlük Aktivite Ekle
          </button>
          <button style={{ width: "100%" }} onClick={() => this.degistirHandler("GunlukAktiviteRaporuListele")}>
            Günlük Aktivite Listele
          </button>
        </div>
        <div style={{ width: "85%", height: "100%" }}>{ekran}</div>
      </div>
    );
  }
}

export default App;
