import React,{Component} from 'react';
import axios from 'axios';
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

class RaporAyarlar extends Component{
    constructor(props)
    {
        super(props);
        this.state={
            RaporSecenek:""
        }
    }
    Error = mesaj => {
        toast.error(mesaj);
      };
      Success = mesaj => {
        toast.success(mesaj);
      };

    yeniRaporSecenek = () =>{
        if(this.state.RaporSecenek === null || this.state.RaporSecenek.length<=0)
        {
            this.Error("Bu Alana Boş Bırakılamaz");
            return;
        }
        axios.post("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/yeniRaporSecenekEkle",this.state,{headers:{
            "Content-Type": "application/json",
          authorization: this.props.token,
        }}).then(response => response.data).then(result => {
            if(result.success === false)
            {
                this.Error("Rapor Seçenek Eklerken Hata Yaşandı -> "+result.mesaj);
            }
            else
            {
                this.Success(result.mesaj);
            }
        }).catch(err =>{
            this.Error("Sorgu Hatası -> "+ err);
        })
    }

    render(){

        return(<div>
            <ToastContainer/>
            <label>Yeni Rapor Seçeneği Giriniz</label>
            <input value={this.state.RaporSecenek} onChange={event => this.setState({RaporSecenek: event.target.value})}/>
            <button onClick={this.yeniRaporSecenek}>Kayıt Et</button>
        </div>)
    }
}

export default RaporAyarlar;