import React, { Component } from "react";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";


class GunlukAktiviteRaporu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      Rapor1: "",
      Rapor2: "",
      Rapor3: "",
      Rapor4: "",
      Rapor5: "",
      Secenekler: [],
      SecilenRaporSecenek:""
    };
  }

  Error = mesaj => {
    toast.error(mesaj);
  };
  Success = mesaj => {
    toast.success(mesaj);
  };

  raporGonderHandler = () => {
    const Ad = this.props.Ad;
    const Soyad = this.props.Soyad;
    const Tarih = new Date();
    const RaporSecenek = this.state.SecilenRaporSecenek;
    let Rapor = [];
    //const Tarih = new Date().toISOString().split("T")[0];
    for (let i in this.state) {
      if (i === "Secenekler" || i === "SecilenRaporSecenek") {
        continue;
      }
      Rapor.push({ Rapor: this.state[i] });
    }
    const data = {
      id: this.props._id,
      Ad: Ad,
      Soyad: Soyad,
      Tarih: Tarih,
      RaporSecenek: RaporSecenek,
      Rapor: Rapor
    };
    axios
      .post("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/gunlukAktiviteRaporuEkle", data, {
        headers: {
          "Content-Type": "application/json",
          authorization: this.props.token
        }
      })
      .then(response => response.data)
      .then(response => {
        if (response.success === true) {
          this.Success(response.mesaj);
        } else {
          this.Error(response.mesaj);
        }
      })
      .catch(err => this.Error("Hata Yaşandı -> " + err));
  };

  componentDidMount() {
    axios
      .get("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/raporSeceneklerGetir", {
        headers: {
          "Content-Type": "application/json",
          authorization: this.props.token
        }
      })
      .then(response => response.data)
      .then(response => {
        if (response.success === false) {
          this.Error(response.mesaj);
          this.setState({ Secenekler: null });
          return;
        }
        this.setState({
          Secenekler: (
            <select style={{ alignSelf: "left" }}>
              {response.map((Secenek, index) => {
                return (
                  <option
                    key={index}
                    value={Secenek}
                    onClick={() =>
                      this.setState({
                        SecilenRaporSecenek: Secenek.RaporSecenek
                      })
                    }
                  >
                    {Secenek.RaporSecenek}
                  </option>
                );
              })}
            </select>
          )
        });
        this.Success("Rapor Seçenekler Getirildi");
      });
  }

  render() {
    return (
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          marginBottom: "15px",
          margin: "0px",
          border: "2px solid #ccc",
          padding: "5px"
        }}
      >
        <ToastContainer />
        <h1 style={{ marginBottom: "80px" }}>Günlük Aktivite Raporu</h1>
        <div style={{ marginRight: "30 px" }}>
          {this.state.Secenekler !== null ? (
            this.state.Secenekler
          ) : (
            <p>Secnekleri Görebilmek için Giriş yapınız!</p>
          )}
        </div>
        <label>1. Yapılan</label>
        <textarea
          name="Rapor1"
          value={this.state.Rapor1}
          onChange={event => this.setState({ Rapor1: event.target.value })}
          style={{
            width: "500px",
            height: "200px",
            marginBottom: "15px",
            display: "inline-block",
            margin: "0px",
            border: "2px solid #ccc",
            padding: "5px"
          }}
        />
        <label>2. Yapılan</label>
        <textarea
          name="Rapor2"
          value={this.state.Rapor2}
          onChange={event => this.setState({ Rapor2: event.target.value })}
          style={{
            width: "500px",
            height: "200px",
            marginBottom: "15px",
            display: "inline-block",
            margin: "0px",
            border: "2px solid #ccc",
            padding: "5px"
          }}
        />
        <label>3. Yapılan</label>
        <textarea
          name="Rapor3"
          value={this.state.Rapor3}
          onChange={event => this.setState({ Rapor3: event.target.value })}
          style={{
            width: "500px",
            height: "200px",
            marginBottom: "15px",
            display: "inline-block",
            margin: "0px",
            border: "2px solid #ccc",
            padding: "5px"
          }}
        />
        <label>4. Yapılan</label>
        <textarea
          name="Rapor4"
          value={this.state.Rapor4}
          onChange={event => this.setState({ Rapor4: event.target.value })}
          style={{
            width: "500px",
            height: "200px",
            marginBottom: "15px",
            display: "inline-block",
            margin: "0px",
            border: "2px solid #ccc",
            padding: "5px"
          }}
        />
        <label>5. Yapılan</label>
        <textarea
          name="Rapor5"
          value={this.state.Rapor5}
          onChange={event => this.setState({ Rapor5: event.target.value })}
          style={{
            width: "500px",
            height: "200px",
            marginBottom: "15px",
            display: "inline-block",
            margin: "0px",
            border: "2px solid #ccc",
            padding: "5px"
          }}
        />
        <button onClick={this.raporGonderHandler}>Raporu Gönder</button>
      </div>
    );
  }
}

export default GunlukAktiviteRaporu;
