import React, { Component } from "react";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
//NOT: buraya etkisi belli olamayan bir props kullanıldı bu props APP.js deki timer yok
//ediyor eğer hata yaşanırsa bundan olabilir ama kesin değil
class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      kullanici: "",
      sifre: "",
      token: ""
    };
    
  }
  URL = "ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010";
  Error = mesaj => {
    toast.error(mesaj);
  };
  Success = mesaj => {
    toast.success(mesaj);
  };

  handleKullanici = event => {
    this.setState({ kullanici: event.target.value });
  };

  handleSifre = event => {
    this.setState({ sifre: event.target.value });
  };

  handleSubmit = event => {
    let data = {
      kullanici_adi: this.state.kullanici,
      sifre: this.state.sifre,
      role: this.state.role
    };
    axios
      .post("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/login", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => response.data)
      .then(response => this.sonucHandler(response))
      .catch(error => this.Error("Hata Yaşandı -> " + error));
  };

  sonucHandler = response => {
    if (response.token !== undefined) {
      this.props.setToken(response.token);
      this.props.setAd(response.Ad);
      this.props.setSoyad(response.Soyad);
      this.props.set_id(response._id);
      this.setState({ kullanici: "", sifre: "" });
      this.Success(response.mesaj);
      return;
    }
    //this.props.ekran()
    this.props.tokenYokEtHandler();
    this.props.setToken(null);
    this.props.setAd(null);
    this.props.setSoyad(null);
    this.props.set_id(null);
    this.setState({ kullanici: "", sifre: "" });
    this.Error(response.mesaj);
  };
  render() {
    return (
      <div style={{ display: "flex", flexDirection: "column" }}>
        <ToastContainer />
        <h1>Login Sayfası</h1>
        <input
          placeholder="Kullanıcı Adı"
          value={this.state.kullanici}
          name="kullanici"
          type="text"
          onChange={this.handleKullanici}
        />
        <input
          placeholder="Şifre"
          value={this.state.sifre}
          name="sifre"
          type="password"
          onChange={this.handleSifre}
        />
        <div>
          <button onClick={this.handleSubmit}>Token Oluştur</button>
        </div>
      </div>
    );
  }
}

export default Login;
