import React, { Component } from "react";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

class GunlukAktiviteRaporuListele extends Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
    this.state = {
      Kullanicilar: null,
      Tarihler: null,
      SecilenRaporSecenek: null,
      SecilenId:null,
      SorguSonuc: null,
      tarih: new Date(),
      Secenekler: null
    };
  }
  Error = mesaj => {
    toast.error(mesaj);
  };
  Success = mesaj => {
    toast.success(mesaj);
  };

  raporSorgulaHandler = (id, tarih,secenek) => {
    console.log(tarih);
    axios
      .get("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/raporSorgula", {
        headers: {
          "Content-Type": "application/json",
          authorization: this.props.token,
          id: id,
          Tarih: tarih,
          secenek: encodeURI(secenek)
        }
      })
      .then(response => response.data)
      .then(response => {
        if (response === null) {
          this.Error("Veri Bulunmadı");
          this.setState({ SorguSonuc: null });
          return;
        }
        if (response.success === false) {
          this.Error(response.mesaj);
          this.setState({ SorguSonuc: null });
          return;
        }
        this.setState({
          SorguSonuc: response.Rapor.map((data, index) => {
            return (
              <textarea
                value={data.Rapor}
                rows="20"
                cols="5"
                key={index}
                readOnly
              >
              {data.Rapor}              
              </textarea>
            );
          })
        });
        this.Success("Rapor Getirildi");
      });
  };

  tarihGetir = () => {
    axios
      .get("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/tarihlerGetir", {
        headers: {
          "Content-Type": "application/json",
          authorization: this.props.token,
          id: this.state.SecilenId,
          secenek: encodeURI(this.state.SecilenRaporSecenek)
        }
      })
      .then(response => response.data)
      .then(response => {
        if (response.success === false) {
          this.Error(response.mesaj);
          this.setState({ Tarihler: null });
          return;
        }
        this.setState({
          Tarihler: (
            <select style={{ alignSelf: "left" }}>
                            
              {response.map((tarih, index) => {
                return (
                  <option
                    key={index}
                    value={tarih.Tarih}
                    onClick={() =>
                      this.raporSorgulaHandler(
                        this.state.SecilenId,
                        new Date(tarih.Tarih).toISOString(),
                        this.state.SecilenRaporSecenek
                      )
                    }
                  >                  
                    {new Date(tarih.Tarih).toISOString().split("T")[0]}                  
                  </option>
                );
              })}
                          
            </select>
          )
        });
        this.Success("Tarihler Getirildi");
      });
  }; //NOT: ComponentDidMount ta YeniProje deki TumUserHandler fonksiyondaki //if(this.props.token !== null) mantığı yap ki error yeme server bağlanamazken

  componentDidMount() {
    if (this.props.token !== null) {
      axios
        .all([
          axios.get("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/tumUserlar", {
            headers: {
              "Content-Type": "application/json",
              authorization: this.props.token
            }
          }),
          axios.get("http://ec2-3-15-184-160.us-east-2.compute.amazonaws.com:3010/raporSeceneklerGetir", {
            headers: {
              "Content-Type": "application/json",
              authorization: this.props.token
            }
          })
        ])
        .then(
          axios.spread((responseKullanici, responseSecenek) => {
            const User = responseKullanici.data;
            const Secenekler = responseSecenek.data;

            if (User.success === false) {
              this.Error(User.mesaj);
              this.setState({ Kullanicilar: null });
              return;
            }
            this.setState({
              Kullanicilar: (
                <select style={{ alignSelf: "left" }}>
                                
                  {User.map((user, index) => {
                    return (
                      <option
                        key={index}
                        value={user}
                        //onClick={() => this.tarihGetir(user._id)}
                        onClick={() => this.setState({SecilenId:user._id}) }
                      >
                      {user.Ad + " " + user.Soyad}                  
                      </option>
                    );
                  })}
                              
                </select>
              )
            });
            this.Success("Kullanıcılar Getirildi");

            if (Secenekler.success === false) {
              this.Error(Secenekler.mesaj);
              this.setState({ Secenekler: null });
              return;
            }
            this.setState({
              Secenekler: (
                <select style={{ alignSelf: "left" }}>
                                
                  {Secenekler.map((Secenek, index) => {
                    return (
                      <option
                        key={index}
                        value={Secenek}
                        onClick={() =>
                          this.setState({
                            SecilenRaporSecenek: Secenek.RaporSecenek
                          })
                        }
                      >
                      {Secenek.RaporSecenek}              
                      </option>
                    );
                  })}
                              
                </select>
              )
            });
            this.Success("Rapor Seçenekler Getirildi");
          })
        )
        .catch(err => "Hata yaşandı ->" + this.Error(err));
    }
  }

  render() {
    return (
      <div style={{ height: "100%", width: "100%" }}>
                
        <ToastContainer />
                
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            padding: "2px 2px",
            marginTop: "75px"
          }}
        >
                    
          <div style={{ marginRight: "30 px" }}>
                        
            {this.state.Kullanicilar !== null ? (
              this.state.Kullanicilar
            ) : (
              <p>Görevli Listesini Görebilmek için Lütfen Giriş Yapınız!</p>
            )}
                      
          </div>
                    
          <div style={{ marginRight: "30 px" }}>
                        
            {this.state.Secenekler !== null ? (
              this.state.Secenekler
            ) : (
              <p>Secnekleri Görebilmek için Giriş yapınız!</p>
            )}
                      
          </div>
          <div style={{ marginRight: "30 px", marginTop:"2px" }}>
                        
           <button onClick={this.tarihGetir}>Tarihler Bul</button>
                      
          </div>
                    
          <div style={{ marginRight: "30 px" }}>
                        
            {this.state.Tarihler !== null ? (
              this.state.Tarihler
            ) : (
              <p>Tarihleri Görebilmek için bir Kullanıcı Seçin!</p>
            )}
                      
          </div>
                             
        </div>
                
        <div style={{ display: "flex", flexDirection: "column" }}>
                    
          {this.state.SorguSonuc !== null &&
          this.state.SorguSonuc !== undefined ? (
            this.state.SorguSonuc
          ) : (
            <p>Rapor Bulunamadı</p>
          )}
                  
        </div>
                 
      </div>
    );
  }
}

export default GunlukAktiviteRaporuListele;
